.menu__button {
  margin-top: 1rem;
  background-color: transparent;
  color: #a9a9a9;
  cursor: pointer;
  opacity: 1;
  -webkit-transition: opacity .1s cubic-bezier(0,0,.2,1) 0s;
  -o-transition: opacity .1s cubic-bezier(0,0,.2,1) 0s;
  transition: opacity .1s cubic-bezier(0,0,.2,1) 0s;
  will-change: opacity;
}

.menu__button:focus,
.menu__button:hover {
  opacity: .6;
}

.menu__button:active {
  opacity: .4;
}

@media screen and (min-width: 768px) {
  .menu__button {
    margin-top: 0;
  }
}

